import Loading from '../../public/Loading'
import None from '../../public/noNumber'
import Person from '../../public/person'
import Bus from '../../assets/eventBus'
  export default {
    name: "friendsNotice",
    data() {
      return {
        api: this.GLOBAL.baseURL,
        friendList: [],
        noThing:'',
        total:0,
        isLoading:false
      }
    },
    components: {
      Person,
      None,
      Loading
    },
    methods: {
      handleCurrentChange(val){
        this.getFrideng(val)
      },
      agree(item,index) {
        this.$http.post(this.api + '/home/Friend/agree',{
          token: localStorage.getItem('token'),
          notice_id: item.notice_id,
          status : 1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.sendNumber()
              item.isShow = !item.isShow
              item.activeIndex = 1
            }else if(res.data.code == 3){
              alert(res.data.msg)
            }else{
              alert(res.data.msg)
            }
          })
      },
      refuse(item,index){
        this.$http.post(this.api + '/home/Friend/agree',{
          token: localStorage.getItem('token'),
          notice_id: item.notice_id,
          status : 2
        })
          .then(res=>{
            if(res.data.code == 1){
              this.sendNumber()
              item.isShow = !item.isShow
              item.activeIndex = 2
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.refuse(item,index)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      ignore(item,index){
        this.$http.post(this.api + '/home/Friend/agree',{
          token: localStorage.getItem('token'),
          notice_id: item.notice_id,
          status : 3
        })
          .then(res=>{
            if(res.data.code == 1){
              this.sendNumber()
              item.isShow = !item.isShow
              this.friendList.splice(index,1)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.ignore(item,index)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      toPerson(item,index){
        this.$router.push({
          path: '/userdata',
          query: {
            information: item.userId
          }
        })
      },
      sendNumber(){
        Bus.$emit('reduceNotice',1)
      },
      getFrideng(val){
        this.isLoading = true
        this.$http.post(this.api + '/home/Friend/getlist',{
          token: localStorage.getItem('token'),
          page: val
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data.data
              if(data.length){
                var a = []
                for(let i =0;i<data.length;i++){
                  let userImg = ''
                  if(data[i].fuser.head_image){
                    userImg = data[i].fuser.headimage.domain + data[i].fuser.headimage.image_url
                  }else{
                    userImg = '../../../static/img/static/user.png'
                  }
                  if(data[i].status == 0){
                    a.push({
                      imgUrl:userImg,
                      name: data[i].fuser.family_name + data[i].fuser.middle_name + data[i].fuser.name,
                      content: data[i].msg,
                      isShow: true,
                      userId: data[i].fuser.user_id,
                      activeIndex:	data[i].status,
                      notice_id: data[i].notice_id
                    })
                  }else{
                    a.push({
                      imgUrl:userImg,
                      name: data[i].fuser.family_name + data[i].fuser.middle_name + data[i].fuser.name,
                      content: data[i].msg,
                      isShow: false,
                      userId: data[i].fuser.user_id,
                      activeIndex:	data[i].status,
                      notice_id: data[i].notice_id
                    })
                  }
                }
                this.friendList = a
              }else{
                this.noThing = '暂无数据'
              }
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getFrideng(val)
                })
            }else if(res.data.code == 0){
              this.isLoading = false
              alert(res.data.msg)
            }
          })
      }
    },
    mounted() {
      let _this = this
      _this.getFrideng(1)
    }
  }



